import React from "react";
import { data } from "../utils/data";

export const NetworkTab =()=>{    
    return (
        <div className="">
                <select className="focus:outline-none p-0">
                    {
                        data.map((item)=>{
                            console.log(item.icon)
                           return (  

                               <option> 
                                <img 
                                src={item.icon} 
                                alt="Girl in a jacket" 
                                width="500"
                                height="600" />
                               {item.name}  </option>
                           
                           )
                        })
                    }       
                </select> 


        </div>
    )
}